import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';

import { ItemReturn } from '@biz-nest/commerce-client';
import {
  AppNameService,
  EventbusService, ItemReturnListBase, MainBarTitleService, WindowService
} from '@front-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { AdminDateFilterFieldComponent } from '../../../misc/admin-date-filter-field/admin-date-filter-field.component';
import { AdminModalContainerComponent } from '../../../misc/admin-modal-container/admin-modal-container.component';
import { AdminPageContentComponent } from '../../../misc/admin-page-content/admin-page-content.component';
import { AdminSearchFieldComponent } from '../../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../../misc/admin-top-bar/admin-top-bar.component';
import { UpdateItemReturnComponent } from '../update-item-return/update-item-return.component';
import { ViewItemReturnComponent } from '../view-item-return/view-item-return.component';

@Component({
  selector: 'app-item-return-list',
  standalone: true,
  imports: [
    RouterModule,
    AdminTableComponent,
    AdminPageContentComponent,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent,
    ViewItemReturnComponent,
    AdminModalContainerComponent,
    UpdateItemReturnComponent
  ],
  templateUrl: './item-return-list.component.html',
  styleUrl: './item-return-list.component.scss'
})
export class ItemReturnListComponent
  extends ItemReturnListBase
  implements OnInit, OnDestroy {
  viewModalOptions = {
    showModal: false,
    modalTitle: 'View Item Return',
    modalMsg: 'Are you sure you want to view this item?',
    modalCancelButton: true,
    modalCancelText: 'Cancel',
    modalConfirmText: 'View',
    modalConfirmButton: true
  };

  updateModalOptions = {
    showModal: false,
    modalTitle: 'Update Item Return',
    modalMsg: 'Are you sure you want to update this item?',
    modalCancelButton: true,
    modalCancelText: 'Cancel',
    modalConfirmText: 'Update',
    modalConfirmButton: true
  };

  itemReturn?: ItemReturn;

  constructor(
    protected override windowService: WindowService,
    protected override toastr: ToastrService,
    protected override router: Router,
    protected override eventbus: EventbusService,
    protected override apNme: AppNameService,
    public override mainBarTitle: MainBarTitleService,
    protected override metaTagService: Meta,
    protected override titleService: Title
  ) {
    super(windowService, toastr, router, eventbus, apNme, mainBarTitle, metaTagService, titleService);
  }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.listCompose.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.init();
  }


  ngOnDestroy(): void {
    this.destroy();
  }

  viewItemReturn(itemReturn: ItemReturn) {
    this.eventbus.generalEvent$.next({
      type: 'oneItem',
      data: itemReturn
    });
    this.viewModalOptions.showModal = true;
    this.itemReturn = itemReturn;
  }

  updateItemReturn(itemReturn: ItemReturn) {
    this.eventbus.generalEvent$.next({
      type: 'updateItemReturn',
      data: itemReturn
    });
    this.updateModalOptions.showModal = true;
    this.itemReturn = itemReturn;
  }

  toggleModalView() {
    this.viewModalOptions.showModal = !this.viewModalOptions.showModal;
  }

  toggleModalUpdate() {
    this.updateModalOptions.showModal = !this.updateModalOptions.showModal;
  }
}
